import React, { Component } from "react";
import config from "../config";
import axios from "axios";

import Onboarding from "./Onboarding";
import Home from "./Home";

class Intro extends Component {
  state = {
    user: this.props.loggedInUser,
    randomMedia: "",
    randomMediaType: "",
    list: [],
  };

  componentDidMount() {
    axios
      .get(`${config.API_URL}/userData`, { withCredentials: true })
      .then((res) => {

        // console.log(this.props)
        this.setState({
          list: res.data.list,
        });

        let rated = [];

        if (this.state.list.length !== 0) {
          rated = this.state.list.filter((media) => {
            return (media.listType = "rated");
          });
        }

        if (rated.length < 10) {
          this.getRandomMedia();
        }
      })
      .catch((err) => {
        console.log("CDM err" + err);
      });
  }

  getRandomMedia = () => {
    console.log("get random media console log");
    let random = Math.floor(Math.random() * 2);
    let mediaType;

    random === 1 ? (mediaType = "movie") : (mediaType = "tv");

    axios
      .get(
        `https://api.themoviedb.org/3/${mediaType}/popular?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&page=1`
      )
      .then((res) => {
        let results = res.data.results.filter((media) => {
          return media.poster_path;
        });

        let randomNum = Math.floor(Math.random() * results.length);

        // console.log(randomNum);
        // console.log(results);
        console.log(results[randomNum]);

        //FIX DUPLICATE MEDIAS

        // for (let i = 0; i < results.length; i++) {
        //   if (this.state.list && this.state.list.some(
        //     e => { return e.apiId === results[i].id }
        //   )) {
        //     randomNum = i;
        //   }
        // }

        this.setState({
          randomMedia: results[randomNum],
          randomMediaType: mediaType,
        });
      })
      .catch((err) => {
        console.log("getrandom err" + err);
      });
  };

  handleRate = (e) => {
    e.preventDefault();
    let rating = e.target.innerHTML;
    console.log('rating ', rating);
    // console.log(this.state.randomMediaType);
    const { randomMediaType, randomMedia} = this.state
    axios
      .post(
        `${config.API_URL}/create`,
        {
          mediaType: randomMediaType,
          apiId: randomMedia.id,
          listType: "rated",
          rating,
          description: randomMedia.overview,
          image: randomMedia.poster_path,
          title:
            randomMediaType === "movie"
              ? randomMedia.title
              : randomMedia.name,
          genres: randomMedia.genre_ids,
        },
        { withCredentials: true }
      )
      .then((response) => {
        console.log(response.data.list);
        
        this.setState({
          list: response.data.list,
          randomMedia: "",
          randomMediaType: "",
        });

        let rated = this.state.list.filter((media) => {
          return media.listType === "rated";
        });

        if (rated.length < 10) {
          this.getRandomMedia();
        }
      })
      .catch((err) => {
        console.log("create media err" + err);
      });
  };

  handleSkip = () => {
    console.log('skipping')
    const { randomMediaType, randomMedia} = this.state
    axios
      .post(
        `${config.API_URL}/create`,
        {
          mediaType: randomMediaType,
          apiId: randomMedia.id,
          listType: "skipped",
          description: randomMedia.overview,
          image: randomMedia.poster_path,
          title:
            randomMediaType === "movie"
              ? randomMedia.title
              : randomMedia.name,
          genres: randomMedia.genre_ids,
        },
        { withCredentials: true }
      )
      .then((response1) => {
        let clonedState = JSON.parse(JSON.stringify(this.state.list))
   
        clonedState.length = response1.data.list.length
        for (let i = 0; i < response1.data.list.length; i++) {
          clonedState[i] = response1.data.list[i]
          clonedState[i].listType = 'skipped'
          console.log(clonedState[i])
        }
        console.log(clonedState)

        this.setState({
          list: clonedState,
          randomMedia: "",
          randomMediaType: "",
        }, () => { 
          let rated = this.state.list.filter((media) => {
            return media.listType === "rated";
          });
  
          if (rated.length < 10) {
            this.getRandomMedia();
          }
        });

        
      })
      .catch((err) => {
        console.log("skip err" + err);
      });
  };

  render() {
    const { list } = this.state;
    // console.log(list)
    let rated = list.filter((media) => {
      return (media.listType = "rated");
    });

    // if (!list.length) {
    //   return <div>Loading . . . </div>
    // }

    return (
      <>
        {rated.length < 10 ? (
          <Onboarding
            ratedList={rated}
            random={this.state.randomMedia}
            onRate={this.handleRate}
            onSkip={this.handleSkip}
          />
        ) : (
            <Home {...this.props} />
        )}
      </>
    );
  }
}
export default Intro;
